import axios from 'axios'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import HideId from '@/components/HideId.vue'
import DateDisplay from '@/components/DateDisplay.vue'
import ProductDetailDisplay from '@/components/ProductDetailDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        HideId,
        DateDisplay,
        ProductDetailDisplay,
    },
    props: ['productNo'],
    data() {
        return {
            editor: ClassicEditor,
            editorConfig: {
                ckfinder: {},
                toolbar: {
                    shouldNotGroupWhenFull: true
                }
            },
            admin: true,
            dialog: false,
            haveOrder: false,
            totalContents: 0,
            contents: [],
            options: {},
            loading: true,
            headers: [{
                text: '번호',
                value: 'reviewNo',
                width: '7%',
                align: 'center',
                divider: true
            }, {
                text: '이미지',
                value: 'image',
                width: '10%',
                align: 'center',
                divider: true
            }, {
                text: '별점',
                value: 'star',
                width: '10%',
                align: 'center',
                divider: true
            }, {
                text: '후기',
                value: 'content',
                width: '58%',
                align: 'center',
                divider: true
            }, {
                text: '작성자',
                value: 'id',
                width: '7%',
                align: 'center',
                divider: true
            }, {
                text: '작성일',
                value: 'regDate',
                width: '8%',
                align: 'center'
            }],
            searches: [{
                text: '작성자',
                value: 'id'
            }],
            search: 'id',
            searchWord: '',
            star: 5,
            content: '',
            contentColor: 'black--text',
            imageFile: null,
            imageUrl: null,

        }
    },
    methods: {
        getReview() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options;
            axios({
                method: 'get',
                url: `/api/review/getAllReviews`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    search: this.search,
                    searchWord: this.searchWord,
                    productNo: this.productNo
                }
            }).then(res => {
                this.contents = res.data.reviewList;
                this.totalContents = res.data.count;
            }).finally(() => {
                this.loading = false;
            })
        },
        searchReview() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getReview();
            }
        },
        // 이미지
        fileInputClick() {
            document.getElementById(`fileInput`).click();
        },
        onImageChange() {
            const file = this.imageFile;
            if (file) {
                this.imageUrl = URL.createObjectURL(file);
                URL.revokeObjectURL(file);
            } else {
                this.imageUrl = null;
            }
        },

        addReview() {
            if (this.content == '') {
                alert('후기를 입력해주세요');
                return;
            }
            let data = {
                productNo: this.productNo,
                star: this.star,
                content: this.content,
                image: this.imageFile.name,
                id: this.getLogin.user.id,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            formData.append(`fileList`, this.imageFile);
            axios.post(`/api/review/insert`, formData)
                .then(() => {
                    this.dialog = false;
                    this.content = '';
                    alert("리뷰 등록 완료");
                    this.getReview();
                }).catch((err) => {
                    alert('리뷰 작성에 실패했습니다.')
                    console.log(err);
                })
        },
        resetReview() {
            this.star = 5;
            this.content = '';
            this.imageFile = null;
            this.imageUrl = null;
            this.dialog = false;
        },
        deleteReview(num) {
            axios.delete(`/api/review/delete/${num}`)
                .then(() => {
                    alert("삭제가 완료되었습니다.")
                    this.getReview();
                })
        },
        updateReview(num) {
            this.$router.push(`/updatePost/review/${num}`);
        },
        idCheck() {
            this.haveOrder = false;
            axios({
                    method: 'get',
                    url: `/api/order/getCountToReview`,
                    params: {
                        id: this.getLogin.user.id,
                        productNo: this.productNo,
                    }
                })
                .then(res => {
                    if (res.data > 0) {
                        this.haveOrder = true;
                    }
                })
        },

    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getReview();
            },
            deep: true
        },
        content: {
            handler() {
                if (this.content.length > 600) {
                    this.contentColor = 'red--text';
                } else {
                    this.contentColor = 'black--text';
                }
            }
        }
    },
    mounted() {
        this.idCheck();
    }
}
