export default {
    data() {
        return {
            pagename: '',
            post: true,
        }
    },
    methods: {
        currentURL() {
            this.post = true;
            let link = document.location.href;
            let pageList = [{
                text: '공지사항',
                value: 'notice',
            }, {
                text: '자주 묻는 질문',
                value: 'faq',
            }, {
                text: '후기',
                value: 'review',
            }, ]
            for (let i = 0; i < pageList.length; i++) {
                if (link.indexOf(pageList[i].value) != -1) {
                    this.pagename = pageList[i].text;
                }
            }
            if (link.indexOf('noticePost') != -1) {
                this.post = false;
            }
        },
    },
    computed: {},
    watch: {
        '$route'() {
            this.currentURL();
        }
    },
    mounted() {
        this.currentURL();
    }
}
