import axios from 'axios';
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {},
    data() {
        return {
            dialog: false,
            dialog2: false,
            overlap: 0,
            loading: false,
            options: {},
            headers: [{
                text: '이미지',
                value: 'image',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '40%',
            }, {
                text: '적립금',
                value: 'point',
                divider: true,
                align: 'center',
                width: '12%',
            }, {
                text: '가격',
                value: 'price',
                divider: true,
                align: 'center',
                width: '18%',
            }, {
                text: '선택',
                value: 'selection',
                align: 'center',
                width: '10%',
            }],
            selected: [],
            wishLists: [],
            totalContents: 0,
            colorOption: null,
            colorSelection: null,
            sizeOption: null,
            sizeSelection: null,
            basketAmount: 1,
            selectedItem: '',

            numberRule: val => {
                if (val == '') return '개수를 입력해주세요'
                return true
            },
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        getWishList() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                    method: 'get',
                    url: `/api/wishList/getWishList`,
                    params: {
                        page: page,
                        perPage: itemsPerPage,
                        id: this.getLogin.user.id,
                    }
                })
                .then(res => {
                    this.wishLists = res.data.wishLists;
                    this.totalContents = res.data.count;
                }).finally(
                    this.loading = false
                )
        },
        deleteWishList(item) {
            let deletes = [];
            if (item == 'selected') {
                console.log(this.selected);
                for (let i = 0; i < this.selected.length; i++) {
                    let data = {
                        id: this.getLogin.user.id,
                        productNo: this.selected[i].productNo,
                    }
                    deletes.push(data);
                }
            } else {
                console.log('test');
                console.log(item);
                let data = {
                    id: this.getLogin.user.id,
                    productNo: item.productNo,
                }
                deletes.push(data);
            }

            axios.delete(`/api/wishList/deleteWishList`, {
                    data: deletes
                })
                .then(() => {
                    alert('선택한 관심상품이 삭제되었습니다.');
                    this.getWishList();
                }).catch(err => {
                    alert('삭제에 실패하였습니다');
                    console.log(err);
                })
        },
        openDialog(item) {
            this.colorOption = null;
            this.colorSelection = null;
            this.sizeOption = null;
            this.sizeSelection = null;
            if (item.color != null) {
                this.colorOption = item.color.split(';');
            }
            if (item.size != null) {
                this.sizeOption = item.size.split(';');
            }
            this.selectedItem = item;
            this.dialog = true;
        },
        reset() {
            this.colorOption = null;
            this.colorSelection = null;
            this.sizeOption = null;
            this.sizeSelection = null;
            this.basketAmount = 1;
            this.dialog2 = false;
            this.dialog = false;
            this.getWishList();
        },
        buyItNow() {
            let selection = [];
            let data = {
                id: this.getLogin.user.id,
                productNo: this.selectedItem.productNo,
                selectedColor: this.colorSelection,
                selectedSize: this.sizeSelection,
                basketAmount: this.basketAmount,
                price: this.selectedItem.price,
                discount: this.selectedItem.discount,
            }
            selection.push(data);
            this.$router.push({
                name: "Payment",
                params: {
                    Payment: selection
                }
            });
        },
        addToBasket() {
            let selection = [];
            let data = {
                id: this.getLogin.user.id,
                productNo: this.selectedItem.productNo,
                selectedColor: this.colorSelection,
                selectedSize: this.sizeSelection,
                basketAmount: this.basketAmount,
                price: this.selectedItem.price,
                discount: this.selectedItem.discount,
            }
            selection.push(data);
            axios.get(`/api/basket/getBasketCount/${this.getLogin.user.id}`)
                .then(res => {
                    if (res.data == 50) {
                        alert('장바구니에는 50개까지만 저장이 가능합니다.')
                    } else {
                        axios.post(`/api/basket/insert`, selection)
                            .then(res => {
                                if (res.data > 0) {
                                    alert('저장에 실패하셨습니다');
                                } else {
                                    this.dialog2 = true;
                                }
                            }).catch((err) => {
                                alert('저장에 실패하셨습니다');
                                console.log(err);
                            })
                    }
                })
        },
        amountFilter() {
            if (!(this.basketAmount > 0 && this.basketAmount == Math.round(this.basketAmount))) {
                alert('잘못된 입력입니다.');
                this.basketAmount = 1;
            }
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getWishList()
            },
            deep: true,
        },
    },
}
