import axios from 'axios'
import ProductDetailDisplay from '@/components/ProductDetailDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        ProductDetailDisplay,
    },
    name: 'Payment',
    data() {
        return {
            baskets: [],
            selected: [],
            totalPrice: 0,
            loading: true,
            options: {
                itemsPerPage: 50,
            },
            headers: [{
                text: '상품정보',
                value: 'info',
                divider: true,
                align: 'center',
                width: '55%',
            }, {
                text: '색상',
                value: 'selectedColor',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '사이즈',
                value: 'selectedSize',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '개수',
                value: 'basketAmount',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '합계',
                value: 'totalPrice',
                divider: true,
                align: 'center',
                width: '15%',
            }, ],
            numberRule: val => {
                if (val == '') return '개수를 입력해주세요'
                return true
            },
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        getBasket() {
            this.loading = true;
            axios.get(`/api/basket/getBasket/${this.getLogin.user.id}`)
                .then(res => {
                    this.baskets = res.data;
                }).finally(
                    this.loading = false
                )
        },
        amountFilter(item) {
            if (!(item.basketAmount > 0 && item.basketAmount == Math.round(item.basketAmount))) {
                alert('잘못된 입력입니다.');
                this.$router.go();
                return;
            } else if (item.basketAmount > 9999) {
                alert('주문량이 너무 많습니다');
                this.$router.go();
                return;
            } else if (item.basketAmount > item.amount) {
                alert('재고보다 주문량이 많습니다');
                this.$router.go();
                return;
            } else {
                axios({
                        method: 'patch',
                        url: `/api/basket/updateBasketAmount`,
                        params: {
                            basketIdx: item.basketIdx,
                            basketAmount: item.basketAmount
                        }
                    })
                    .catch(err => {
                        alert('변경에 실패하였습니다')
                        console.log(err);
                    })

            }
        },
        selectAll() {
            if (this.selected.length != this.baskets.length) {
                this.selected = this.baskets;
            } else {
                this.selected = [];
            }
        },
        deleteSelected() {
            let deletes = [];
            for (let i = 0; i < this.selected.length; i++) {
                deletes.push(this.selected[i].basketIdx);
            }
            axios.delete(`/api/basket/deleteBasket`, {
                    data: deletes
                })
                .then(() => {
                    alert('선택한 장바구니가 삭제되었습니다.');
                    this.getBasket();
                }).catch(err => {
                    alert('삭제에 실패하였습니다');
                    console.log(err);
                })
        },
        goToOrder() {
            if (this.selected.length == 0) {
                alert('구매할 상품이 없습니다');
                return;
            }
            this.$router.push({
                name: "Payment",
                params: {
                    Payment: this.selected
                }
            });
        }
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        selected: {
            handler() {
                this.totalPrice = 0;
                for (let i = 0; i < this.selected.length; i++) {
                    if (this.selected[i].onSale == false || this.selected[i].amount == 0) {
                        this.selected.splice(i, 1);
                        alert('품절 상품입니다');
                    } else if (this.selected[i].basketAmount > this.selected[i].amount) {
                        this.selected.splice(i, 1);
                        alert('재고보다 주문량이 많습니다');
                    } else {
                        this.totalPrice += (this.selected[i].price - this.selected[i].discount) * this.selected[i].basketAmount;
                    }
                }
            }
        }
    },
    mounted() {
        this.getBasket();
    }
}
