import axios from 'axios'
import HideId from '@/components/HideId.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        HideId,
    },
    data() {
        return {
            totalContents: 0,
            contents: [],
            options: {},
            loading: true,
            headers: [{
                    text: '번호',
                    value: 'noticeNo',
                    width: '10%',
                    align: 'center',
                    divider: true
                },
                {
                    text: '제목',
                    value: 'title',
                    width: '60%',
                    align: 'center',
                    divider: true
                },
                {
                    text: '작성자',
                    value: 'id',
                    width: '10%',
                    align: 'center'
                },
            ],
            searches: [{
                text: '제목',
                value: 'title'
            }, ],
            search: 'title',
            searchWord: '',
        }
    },
    methods: {
        getNotice() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                    method: 'get',
                    url: `/api/notice/getNotice`,
                    params: {
                        page: page,
                        perPage: itemsPerPage,
                        searchWord: this.searchWord,
                    }
                })
                .then(res => {
                    this.contents = res.data.noticeList;
                    this.totalContents = res.data.count;
                }).finally(
                    this.loading = false
                )
        },
        searchNotice() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getNotice();
            }
        },
        moveto(item) {
            this.$router.push(`/community/noticePost/${item.noticeNo}`)
        },

    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: { //변수 값이 변경될 때 연산을 처리하거나 변수 값에 따라 화면을 제어할 때 사용
        options: {
            handler() {
                this.getNotice()
            },
            deep: true,
        },
    },
}
