import axios from 'axios'
export default {
    data() {
        return {
            mainCategory: '',
            subCategory: '',
            selectedCategory: '',

            page: 1,
            itemsPerPage: 12,
            pageLength: 0,
            visibleLength: 5,

            bestProducts: [],
            products: [],
        }
    },
    methods: {
        setSubCategory() {
            this.subCategory = [{
                text: 'ALL',
                value: 'all',
                type: 'all'
            }, {
                text: '자켓',
                value: 'jacket',
                type: 'outer',
            }, {
                text: '가디건',
                value: 'cardigan',
                type: 'outer',
            }, {
                text: '점퍼',
                value: 'jumper',
                type: 'outer',
            }, {
                text: '코트',
                value: 'coat',
                type: 'outer',
            }, {
                text: '슬리브리스',
                value: 'sleeveless',
                type: 'top',
            }, {
                text: '티셔츠',
                value: 'tshirts',
                type: 'top',
            }, {
                text: '니트',
                value: 'knit',
                type: 'top',
            }, {
                text: '셔츠',
                value: 'shirt',
                type: 'top',
            }, {
                text: '블라우스',
                value: 'blouse',
                type: 'top',
            }, {
                text: '맨투맨',
                value: 'mtm',
                type: 'top',
            }, {
                text: '슬랙스',
                value: 'slacks',
                type: 'pants',
            }, {
                text: '데님',
                value: 'denim',
                type: 'pants',
            }, {
                text: '면',
                value: 'cotton',
                type: 'pants',
            }, {
                text: 'SHORT',
                value: 'shorts',
                type: 'pants',
            }, {
                text: '미니',
                value: 'mini',
                type: 'skirt',
            }, {
                text: '미디/롱',
                value: 'midi-long',
                type: 'skirt',
            }, {
                text: '원피스',
                value: 'ops',
                type: 'dress'
            }, {
                text: '점프수트',
                value: 'jumpSuit',
                type: 'dress'
            }, ];
            for (let i = this.subCategory.length - 1; i > 0; i--) {
                if (this.mainCategory != this.subCategory[i].type) {
                    this.subCategory.splice(i, 1);
                }
            }
        },
        getProductList() {
            axios({
                method: 'get',
                url: `/api/product/getBestProductList`,
                params: {
                    type1: this.mainCategory,
                    type2: this.selectedCategory
                }
            }).then(res => {
                this.bestProducts = res.data;
            }).catch((err) => {
                console.log(err);
            })
            axios({
                method: 'get',
                url: `/api/product/getProductList`,
                params: {
                    page: this.page,
                    perPage: this.itemsPerPage,
                    type1: this.mainCategory,
                    type2: this.selectedCategory,
                    minPrice: 0,
                    maxPrice: 99999999,
                }
            }).then(res => {
                this.products = res.data.productList;
                this.pageLength = Math.ceil(res.data.count / this.itemsPerPage);
            }).catch((err) => {
                console.log(err);
            })
        },
        searchProductList() {
            if (this.page != 1) {
                this.page = 1;
            } else {
                this.getProductList();
            }
        },
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        moveToDetail(num) {
            this.$router.push(`/productDetail/${num}`)
        },
    },
    watch: {
        '$route'() {
            this.mainCategory = this.$route.params.id;
            this.selectedCategory = this.$route.params.sub;
            this.searchProductList();
            this.setSubCategory();
        },
        page: {
            handler() {
                this.getProductList();
            }
        }
    },
    mounted() {
        this.mainCategory = this.$route.params.id;
        this.selectedCategory = this.$route.params.sub;
        this.getProductList();
        this.setSubCategory();
    }
}
