import axios from 'axios'
export default {
    data() {
        return {
            sales: [],
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '상품번호',
                value: 'productNo',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '이미지',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '30%',
            }, {
                text: '가격',
                value: 'priceSum',
                align: 'center',
                width: '40%',
            }, ],

            searches: [{
                text: '상품번호',
                value: 'productNo',
            }, {
                text: '상품명',
                value: 'productName',
            }, {
                text: '판매금액',
                value: 'priceSum',
            }, {
                text: '주문 날짜',
                value: 'orderDate',
            }, ],
            search: 'orderDate',
            searchWord1: '',
            searchWord2: '',
            totalPrice: 0,

            menu1: false,
            menu2: false,
        }
    },
    methods: {
        getSales() {
            this.sales = [];
            this.totalContents = 0;
            this.totalPrice = 0;
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/order/getSalesSettlement`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    search: this.search,
                    searchWord1: this.searchWord1,
                    searchWord2: this.searchWord2,
                }
            }).then(res => {
                this.sales = res.data.salesList;
                this.totalContents = res.data.countList.length;
                for (let i = 0; i < res.data.countList.length; i++) {
                    this.totalPrice += res.data.countList[i];
                }
            }).catch((err) => {
                console.log(err);
            }).finally(
                this.loading = false
            )
        },
        searchSales() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getSales();
            }
        },
        reset() {
            this.searchWord1 = null;
            this.searchWord2 = null;
            if (this.search == 'priceSum') {
                this.searchWord1 = 0;
                this.searchWord2 = 999999999;
            } else if (this.search == 'orderDate') {
                let date = new Date();
                this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getSales();
        },
        searchPolicy() {
            if (this.searchWord1 < 0 || this.searchWord1 > 999999999 || this.searchWord1 != Math.round(this.searchWord1)) {
                alert('가격 제한 : 0원 ~ 999,999,999원');
                this.searchWord1 = 0;
            } else if (this.searchWord2 < 0 || this.searchWord2 > 999999999 || this.searchWord2 != Math.round(this.searchWord2)) {
                alert('가격 제한 : 0원 ~ 999,999,999원');
                this.searchWord2 = 999999999;
            }
        },
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
    },
    watch: { //변수 값이 변경될 때 연산을 처리하거나 변수 값에 따라 화면을 제어할 때 사용
        options: {
            handler() {
                this.getSales();
            },
            deep: true,
        },
        search: {
            handler() {
                this.searchWord1 = '';
                this.searchWord2 = '';
                if (this.search == 'priceSum') {
                    this.searchWord1 = 0;
                    this.searchWord2 = 999999999;
                } else if (this.search == 'orderDate') {
                    let date = new Date();
                    this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                    this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
            }
        },
    },
    mounted() {
        let date = new Date();
        this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
        this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
    }
}
