import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import ProductDetailDisplay from '@/components/ProductDetailDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        DateDisplay,
        ProductDetailDisplay,
    },
    data() {
        return {
            editor: ClassicEditor,
            editorConfig: {
                ckfinder: {},
                toolbar: {
                    shouldNotGroupWhenFull: true
                }
            },
            dialog: false,
            dialog2: false,
            dialog3: false,
            orders: [],
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '주문번호',
                value: 'orderIdx',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '이미지',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '개수',
                value: 'orderAmount',
                divider: true,
                align: 'center',
                width: '8%',
            }, {
                text: '가격',
                value: 'totalPrice',
                divider: true,
                align: 'center',
                width: '12%',
            }, {
                text: '주문 날짜',
                value: 'orderDate',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '주문 상태',
                value: 'state',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '',
                value: 'btn',
                align: 'center',
                width: '15%',
            }, ],

            returnHeaders: [{
                text: '주문번호',
                value: 'orderIdx',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '이미지',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '25%',
            }, {
                text: '개수',
                value: 'orderAmount',
                divider: true,
                align: 'center',
                width: '8%',
            }, {
                text: '가격',
                value: 'totalPrice',
                divider: true,
                align: 'center',
                width: '12%',
            }, {
                text: '주문 날짜',
                value: 'orderDate',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '주문 상태',
                value: 'state',
                align: 'center',
                width: '15%',
            }, ],
            searchWord: '',
            searchDate1: '',
            searchDate2: '',

            states: [],
            stateSelected: null,

            menu1: false,
            menu2: false,

            selectedOrder: '주문 내역조회',
            selectedColor: true,

            purchaseItem: '',

            star: 5,
            content: '',
            contentColor: 'black--text',
            imageFile: null,
            imageUrl: null,
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        colorPicker(put) {
            if (this.selectedOrder == put) {
                return 'secondary'
            }
        },
        selectOrder(put) {
            this.orders = [];
            this.selectedOrder = put;
            this.reset();
        },
        searchOrder(selectedOrder) {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getOrder(selectedOrder);
            }
        },
        getOrder(put) {
            this.loading = true;
            let pageInfo = '';
            if (put == '주문 내역조회') {
                pageInfo = 'orders';
                this.states = [{
                    text: '기준 선택',
                    value: null,
                }, {
                    text: '입금전',
                    value: '입금전',
                }, {
                    text: '결제완료',
                    value: '결제완료',
                }, {
                    text: '배송준비중',
                    value: '배송준비중',
                }, {
                    text: '배송중',
                    value: '배송중',
                }, {
                    text: '배송완료',
                    value: '배송완료',
                }, {
                    text: '구매확정',
                    value: '구매확정',
                }, ];
            } else {
                pageInfo = 'returns';
                this.states = [{
                    text: '기준 선택',
                    value: null,
                }, {
                    text: '취소완료',
                    value: '취소완료',
                }, {
                    text: '교환완료',
                    value: '교환완료',
                }, {
                    text: '환불완료',
                    value: '환불완료',
                }, ];
            }
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/order/getOrderById`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    pageInfo: pageInfo,
                    state: this.stateSelected,
                    searchWord: this.searchWord,
                    searchDate1: this.searchDate1,
                    searchDate2: this.searchDate2,
                    id: this.getLogin.user.id,
                }
            }).then(res => {
                this.orders = res.data.orderList;
                this.totalContents = res.data.count;
            }).catch((err) => {
                this.orders = [];
                this.totalContents = 0;
                console.log(err);
            }).finally(
                this.loading = false
            )
        },
        cancelOrder(item) {
            if (item.state == '입금전' || item.state == '결제완료') {
                let states = [];
                let data = {
                    orderIdx: item.orderIdx,
                    state: '취소완료',
                }
                states.push(data);
                axios.patch(`/api/order/update`, states)
                    .then(res => {
                        if (res.data.length == 0) {
                            alert('주문을 취소하셨습니다');
                            this.dialog2 = false;
                            if (this.options.page != 1) {
                                this.options.page = 1;
                            } else {
                                this.getOrder(this.selectedOrder);
                            }
                        } else {
                            alert(`미완료된 변경(총 ${res.data.length}건)\n주문번호 : ${res.data}`)
                        }
                    }).catch(err => {
                        console.log(err)
                    })
            } else if (item.state == '배송준비중') {
                alert('배송 준비중인 주문입니다. 배송 전 변경/취소 게시판에서 요청해주시기 바랍니다.');
                this.$router.push(`/qna/beforeDeliveryQnA`);
            } else {
                alert('배송 후 교환/반품 게시판에서 요청해주시기 바랍니다.');
                this.$router.push(`/qna/afterDeliveryQnA`);
            }
        },
        reset() {
            this.stateSelected = null;
            this.searchWord = null;
            this.searchDate1 = null;
            this.searchDate2 = null;
            let date = new Date();
            this.searchDate1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
            this.searchDate2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getOrder(this.selectedOrder);
        },
        changeDate(period) {
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate()

            if (period == '1week') {
                if (day > 6) {
                    day = day - 6;
                } else {
                    if (month > 1) {
                        month = month - 1;
                    } else {
                        year = year - 1;
                        month = month + 11;
                    }
                    day = day + 6;
                }
            } else if (period == '1month') {
                if (month > 1) {
                    month = month - 1;
                } else {
                    year = year - 1;
                    month = month + 11;
                }
            } else if (period == '3month') {
                if (month > 3) {
                    month = month - 3;
                } else {
                    year = year - 1;
                    month = month + 9;
                }
            } else if (period == '6month') {
                if (month > 6) {
                    month = month - 6;
                } else {
                    year = year - 1;
                    month = month + 6;
                }
            }
            this.searchDate1 = `${year}-${month}-${day}`;
            this.searchDate2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;

            this.stateSelected = null;
            this.searchWord = null;
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getOrder(this.selectedOrder);
        },
        selectItem(item) {
            this.purchaseItem = item;
        },
        purchaseConfirm() {
            let states = [];
            let data = {
                orderIdx: this.purchaseItem.orderIdx,
                state: '구매확정',
            }
            states.push(data);
            axios.patch(`/api/order/update`, states)
                .then(res => {
                    if (res.data.length == 0) {
                        alert('구매를 확정하셨습니다');
                        if (this.options.page != 1) {
                            this.options.page = 1;
                        } else {
                            this.getOrder(this.selectedOrder);
                        }
                    } else {
                        alert(`미완료된 변경(총 ${res.data.length}건)\n주문번호 : ${res.data}`)
                    }
                }).catch(err => {
                    console.log(err)
                }).finally(() => {
                    this.purchaseItem = '';
                    this.dialog = false;
                })
        },
        // 이미지
        fileInputClick() {
            document.getElementById(`fileInput`).click();
        },
        onImageChange() {
            const file = this.imageFile;
            if (file) {
                this.imageUrl = URL.createObjectURL(file);
                URL.revokeObjectURL(file);
            } else {
                this.imageUrl = null;
            }
        },

        addReview() {
            if (this.content == '') {
                alert('후기를 입력해주세요');
                return;
            }
            let data = {
                productNo: this.purchaseItem.productNo,
                star: this.star,
                content: this.content,
                image: this.imageFile.name,
                id: this.getLogin.user.id,
            };
            let formData = new FormData();
            formData.append('data', new Blob([JSON.stringify(data)], {
                type: "application/json"
            }));
            formData.append(`fileList`, this.imageFile);
            axios.post(`/api/review/insert`, formData)
                .then(() => {
                    this.dialog = false;
                    this.content = '';
                    alert("리뷰 등록 완료");
                    this.getReview();
                }).catch((err) => {
                    alert('리뷰 작성에 실패했습니다.')
                    console.log(err);
                })
        },
        resetReview() {
            this.star = 5;
            this.content = '';
            this.imageFile = null;
            this.imageUrl = null;
            this.dialog3 = false;
        }
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getOrder(this.selectedOrder);
            },
            deep: true,
        },
        content: {
            handler() {
                if (this.content.length > 600) {
                    this.contentColor = 'red--text';
                } else {
                    this.contentColor = 'black--text';
                }
            }
        }
    },
    mounted() {
        let date = new Date();
        this.searchDate1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
        this.searchDate2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
    }
}
