import axios from 'axios'
export default {
    methods: {
        clickImage() {
            document
                .getElementById('imageInput')
                .click();
        },
        select(value) {
            this.isUpdate = false;
            // this.image = value.image;

            this.link = value.link;
            this.num = value.num;
            this.oldImage = value.image;
            // axios
            //     .get(`/api/banner/image/${value.image}`)
            //     .then(res => {
            //         this.file = new File([res.data], value.image, {
            //             type: "image/*",
            //             lastModified: Date.now()
            //         })

            //     })
            //     .catch(err => console.log(err))
            this.image = `/api/banner/image/${value.image}`
            this.file = new File([], value.image, {
                type: "image/*",
                lastModified: Date.now()
            })
        },
        getBanners() {

            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options;
            axios
                .get('/api/banner/getBanners', {
                    params: {
                        page: page,
                        perPage: itemsPerPage
                    }
                })
                .then(res => {
                    this.banners = res.data;
                    this.totalContents = res.data.count;
                })
                .catch(err => {
                    console.log(err.response.status);
                })
                .finally(this.loading = false);
        },
        insertBanner() {
            if (this.link == null)
                this.link = '';
            if (this.num == null)
                this.num = 1;
            let data = {
                image: this.file.name,
                link: this.link,
                num: this.num
            }

            let formData = new FormData();
            formData.append(
                'data',
                new Blob([JSON.stringify(data)], {
                    type: "application/json"
                })
            );
            formData.append(`banner`, this.file);
            axios
                .post('/api/banner/insertBanner', formData)
                .then(() => {
                    this.getBanners();
                    this.file = null;
                    this.link = null;
                    this.num = null;
                    this.image = null;
                })
                .catch((err) => {
                    console.log(err)
                })
        },
        updateBanner() {
            let data = {
                image: this.file.name,
                link: this.link,
                num: this.num
            }
            let formData = new FormData();
            formData.append(
                'data',
                new Blob([JSON.stringify(data)], {
                    type: "application/json"
                })
            );
            formData.append(`banner`, this.file);
            axios.put(`/api/banner/update/${this.oldImage}`, formData)
                .then(() => {
                    this.getBanners();
                    this.file = null;
                    this.link = null;
                    this.num = null;
                    this.image = null;
                    this.isUpdate = true;
                });
        },
        removeBanner() {
            axios.delete(`/api/banner/delete/${this.file.name}`)
                .then(() => {
                    this.getBanners();
                    this.file = null;
                    this.link = null;
                    this.num = null;
                    this.image = null;
                    this.isUpdate = true;
                });
        },
        createImageURL() {
            console.log(this.file)
            const file = this.file;
            if (file) {
                this.image = URL.createObjectURL(file);
                URL.revokeObjectURL(file);
            } else {
                this.image = null;
            }
        },
        indexRule() {
            return false;
        }
    },
    watch: {
        options: {
            handler() {
                this.getBanners();
            },
            deep: true
        }
    },
    data() {
        return {
            loading: false,
            oldImage: null,
            isUpdate: true,
            pageView: 1,
            pageCount: 0,
            banners: [],
            totalContents: 0,
            file: null,
            // images: null,
            image: null,
            link: '',
            num: null,
            options: {
                itemsPerPage: 8
            },
            headers: [{
                text: 'IMAGE',
                value: 'image',
                align: 'center',
                sortable: false,
                divider: true
            }, {
                text: 'LINK',
                value: 'link',
                align: 'center',
                sortable: false,
                divider: true
            }, {
                text: 'INDEX',
                value: 'num',
                align: 'center',
                sortable: false
            }],

            rules: {}
        }
    }
}
