import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        DateDisplay,
    },
    data() {
        return {
            headers: [{
                text: '날짜',
                value: 'pointDate',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '포인트',
                value: 'point',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '내용',
                value: 'content',
                align: 'center',
                width: '60%',
            }, ],
            memberPoint: 0,
            pointList: [],
            options: {},
            totalContents: 0,
            loading: false,

            id: 'tester',
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        getPoint() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                    method: 'get',
                    url: `/api/point/getPointList`,
                    params: {
                        page: page,
                        perPage: itemsPerPage,
                        id: this.getLogin.user.id,
                    }
                })
                .then(res => {
                    this.pointList = res.data.pointList;
                    this.totalContents = res.data.count;
                    this.loading = false;
                })
        },
        getMemberPoint() {
            this.loading = true;
            axios.get(`/api/member/getMemberInfo/${this.getLogin.user.id}`)
                .then(res => {
                    this.memberPoint = res.data.point;
                })
        }
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getPoint();
                this.getMemberPoint();
            },
            deep: true,
        },
    },
}
