import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    data() {
        return {
            states: [{
                state: '입금전',
                count: 0,
            }, {
                state: '결제완료',
                count: 0,
            }, {
                state: '배송준비중',
                count: 0,
            }, {
                state: '배송중',
                count: 0,
            }, {
                state: '배송완료',
                count: 0,
            }, {
                state: '취소완료',
                count: 0,
            }, {
                state: '교환완료',
                count: 0,
            }, {
                state: '환불완료',
                count: 0,
            }, ],
            sum: 0,
            point: '',
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        getData() {
            this.sum = 0;
            axios.get(`/api/order/getOrderGroupByState/${this.getLogin.user.id}`)
                .then(res => {
                    let temp = res.data.orderCounts;
                    for (let i = 0; i < temp.length; i++) {
                        for (let j = 0; j < this.states.length; j++) {
                            if (temp[i].state == this.states[j].state) {
                                this.states[j].count = temp[i].orderNo;
                                this.sum += temp[i].orderNo;
                            }
                        }
                    }
                    this.point = res.data.point;
                }).catch((err) => {
                    for (let j = 0; j < this.states.length; j++) {
                        this.states[j].count = 0;
                    }
                    this.point = 0;
                    console.log(err);
                })
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() {
        this.getData();
    }
}
