import axios from 'axios'
export default {
    methods: {
        getAllMembers() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options;

            axios
                .get('/api/member/getMembers', {
                    params: {
                        page: page,
                        perPage: itemsPerPage,
                        condition: this.condition,
                        param: this.value,
                    }
                })
                .then(res => {
                    this.items = res.data.res;
                    this.totalContents = res.data.count;
                })
                .catch(err => {
                    console.log(err.response.status);
                })
                .finally(this.loading = false);
        },
        searchMember() {
            if (this.options.page != 1) {
                this.options.page = 1
            } else {
                this.getAllMembers();
            }
        },
        telFormatter(tel) {
            let first;
            let second;
            let third;
            if (tel.length == 11) {
                first = tel.substr(0, 3);
                second = tel.substr(3, 4);
                third = tel.substr(7, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.substr(0, 2) == '02' && tel.length == 10) {
                first = tel.substr(0, 2);
                second = tel.substr(2, 4);
                third = tel.substr(6, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.substr(0, 2) == '02' && tel.length == 9) {
                first = tel.substr(0, 2);
                second = tel.substr(2, 3);
                third = tel.substr(5, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.length == 10) {
                first = tel.substr(0, 3);
                second = tel.substr(3, 3);
                third = tel.substr(6, 4);
                return `${first}-${second}-${third}`;
            } else {
                return tel;
            }
        },
        selectItem(item) {
            console.log(item)
            let temp = this.items[
                this
                .items
                .indexOf(item)
            ];
            this.editItem = {
                id: temp.id,
                name: temp.name,
                tel: temp.tel,
                email: temp.email,
                zipcode: temp.zipcode,
                addr1: temp.addr1,
                addr2: temp.addr2,
                point: temp.point
            }
        },
        updateMember() {
            axios.put('/api/member/updateMember', this.editItem)
                .then(() => {
                    this.getAllMembers();
                    this.dialog = false;
                })
        },
        reset() {
            this.condition = 'id';
            this.value = null;
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getAllMembers();
        },

    },
    watch: { //변수 값이 변경될 때 연산을 처리하거나 변수 값에 따라 화면을 제어할 때 사용
        options: {
            handler() {
                this.getAllMembers();
            },
            deep: true,
        },
        search: {
            handler() {
                this.value = '';
            }
        },
    },
    data() {
        return {
            dialog: false,
            totalContents: 0,
            loading: false,
            editItem: {},
            condition: 'id',
            value: '',
            searchType: [{
                text: 'ID',
                value: 'id'
            }, {
                text: '이름',
                value: 'name'
            }, {
                text: '전화번호',
                value: 'tel'
            }, {
                text: '이메일',
                value: 'email'
            }, {
                text: '우편번호',
                value: 'zipcode'
            }, {
                text: '주소1',
                value: 'addr1'
            }, {
                text: '주소2',
                value: 'addr2'
            }, {
                text: '포인트',
                value: 'point'
            }],
            options: {},
            headers: [{
                text: 'ID',
                value: 'id',
                divider: true,
                align: 'center',
                width: '7%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '이름',
                value: 'name',
                divider: true,
                align: 'center',
                width: '7%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '전화번호',
                value: 'tel',
                divider: true,
                align: 'center',
                width: '10%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '이메일',
                value: 'email',
                divider: true,
                align: 'center',
                width: '12%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '우편번호',
                value: 'zipcode',
                divider: true,
                align: 'center',
                width: '7%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '주소1',
                value: 'addr1',
                divider: true,
                align: 'center',
                width: '22%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '주소2',
                value: 'addr2',
                divider: true,
                align: 'center',
                width: '15%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '수신여부',
                value: 'terms',
                divider: true,
                align: 'center',
                width: '8%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '포인트',
                value: 'point',
                divider: true,
                align: 'center',
                width: '6%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '수정',
                value: 'btn',
                align: 'center',
                width: '6%',
                sortable: false,
                class: 'text-subtitle-1'
            }],
            items: []
        }
    }
}
