import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    methods: {
        getData() {
            this.social = false;
            axios.get(`/api/member/getMemberInfo/${this.getLogin.user.id}`)
                .then(res => {
                    this.id = this.getLogin.user.id;
                    this.name = res.data.name;
                    this.zipcode = res.data.zipcode;
                    this.addr1 = res.data.addr1;
                    this.addr2 = res.data.addr2;
                    this.tel = res.data.tel;
                    this.email = res.data.email;
                    this.pwd1 = '';
                    this.pwd2 = '';
                    if (this.id.slice(0, 5) == 'kakao' || this.id.slice(0, 5) == 'naver') {
                        this.social = true;
                    }
                })
        },
        update() {
            let member;
            if (this.pwd1 != null) {
                if (this.pwd1 == this.pwd2) {
                    member = {
                        id: this.id,
                        password: this.pwd1,
                        name: this.name,
                        zipcode: this.zipcode,
                        addr1: this.addr1,
                        addr2: this.addr2,
                        tel: this.tel,
                        email: this.email,
                    }
                }
            } else {
                member = {
                    id: this.id,
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                }
            }
            axios.put('/api/member/updateMember', member)
                .then(() => {
                    alert("수정 성공")
                    this.getData();
                })
                .catch(() => alert("수정 실패"))
        },
        secession() {
            axios.get('/api/member/login', {
                    params: {
                        id: this.id,
                        password: this.password,
                    }
                })
                .then(() => {
                    axios.delete(`/api/member/delete/${this.id}`)
                        .then(() => {
                            alert("탈퇴가 완료되었습니다.");
                            this.Logout();
                            this.$router.push('/')
                        })
                        .catch(() => {
                            alert("배송중인 상품이 있어 탈퇴에 실패했습니다.");
                        })
                })
                .catch(() => {
                    alert("비밀번호가 일치하지 않습니다.")

                })
                .finally(this.$refs.password.reset())
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.extraAddress !== "") {
                        this.addr2 = "";
                    }
                    if (data.userSelectedType === "R") {
                        // 사용자가 도로명 주소를 선택했을 경우
                        this.addr1 = data.roadAddress;
                    } else {
                        // 사용자가 지번 주소를 선택했을 경우(J)
                        this.addr1 = data.jibunAddress;
                    }
                    // // 사용자가 선택한 주소가 도로명 타입일때 참고항목을 조합한다.
                    // if (data.userSelectedType === "R") {
                    //     // 법정동명이 있을 경우 추가한다. (법정리는 제외)
                    //     // 법정동의 경우 마지막 문자가 "동/로/가"로 끝난다.
                    //     if (data.bname !== "" && /[동|로|가]$/g.test(data.bname)) {
                    //         // this.addr2 += data.bname;
                    //     }
                    //     // 건물명이 있고, 공동주택일 경우 추가한다.
                    //     if (data.buildingName !== "" && data.apartment === "Y") {
                    //         this.addr2 +=
                    //             this.addr2 !== "" ?
                    //             `, ${data.buildingName}` :
                    //             data.buildingName;
                    //     }
                    //     // 표시할 참고항목이 있을 경우, 괄호까지 추가한 최종 문자열을 만든다.
                    //     if (this.addr2 !== "") {
                    //         this.addr2 = `(${this.addr2})`;
                    //     }
                    // } else {
                    //     this.addr2 = "";
                    // }
                    // 우편번호를 입력한다.
                    this.zipcode = data.zonecode;
                },
            }).open();
        },
        ...LoginStore.mapActions(['Logout']),
    },
    components: {},
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() {
        if (this.getLogin) {
            this.getData();
        }
    },
    data() {
        return {
            dialog: false,
            id: '',
            pwd1: '',
            pwd2: '',
            name: '',
            zipcode: '',
            addr1: '',
            addr2: '',
            tel: '',
            email: '',
            password: '',
            social: false,
        }
    }
}
