import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
export default {
    components: {
        DateDisplay,
    },
    data() {
        return {
            orders: [],
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '주문번호',
                value: 'orderIdx',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '상품번호',
                value: 'productNo',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '이미지',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '20%',
            }, {
                text: '가격',
                value: 'totalPrice',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '개수',
                value: 'orderAmount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '주문 날짜',
                value: 'orderDate',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '구매자(배송지)',
                value: 'id',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '주문 상태',
                value: 'state',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '상태 변경',
                value: 'stateChange',
                align: 'center',
                width: '10%',
            }, ],

            searches: [{
                text: '주문번호',
                value: 'orderIdx',
            }, {
                text: '상품번호',
                value: 'productNo',
            }, {
                text: '상품명',
                value: 'productName',
            }, {
                text: '주문 날짜',
                value: 'orderDate',
            }, {
                text: 'ID',
                value: 'id',
            }, {
                text: '주문 상태',
                value: 'state',
            }, ],
            search: 'orderIdx',
            searchWord1: '',
            searchWord2: '',

            states: [{
                text: '기준 선택',
                value: null,
                disabled: true,
            }, {
                text: '입금전',
                value: '입금전',
                disabled: true,
            }, {
                text: '결제완료',
                value: '결제완료',
            }, {
                text: '배송준비중',
                value: '배송준비중',
            }, {
                text: '배송중',
                value: '배송중',
            }, {
                text: '배송완료',
                value: '배송완료',
            }, {
                text: '취소완료',
                value: '취소완료',
            }, {
                text: '교환완료',
                value: '교환완료',
            }, {
                text: '환불완료',
                value: '환불완료',
            }, ],
            stateSelected: null,

            stateChanges: [],

            menu1: false,
            menu2: false,

            dialog: false,
            memberInfo: {},
        }
    },
    methods: {
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
        getOrder() {
            this.loading = true;
            this.stateChanges = [];
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/order/getOrder`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    state: this.stateSelected,
                    search: this.search,
                    searchWord1: this.searchWord1,
                    searchWord2: this.searchWord2,
                }
            }).then(res => {
                this.orders = res.data.orderList;
                this.totalContents = res.data.count;
                for (let i = 0; i < this.orders.length; i++) {
                    this.stateChanges.push(this.orders[i].state);
                }
            }).catch((err) => {
                this.orders = [];
                this.totalContents = 0;
                console.log(err);
            }).finally(
                this.loading = false
            )
        },
        searchOrder() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getOrder();
            }
        },
        reset() {
            this.searchWord1 = null;
            this.searchWord2 = null;
            this.stateSelected = null;
            if (this.search == 'orderDate') {
                let date = new Date();
                this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getOrder();
        },
        selectItem(item) {
            this.memberInfo = {
                id: item.id,
                name: item.name,
                tel: item.tel,
                zipCode: item.zipCode,
                address: item.address,
                detailAddr: item.detailAddr,
            }
        },
        stateChange() {
            let states = [];
            for (let i = 0; i < this.orders.length; i++) {
                if (this.orders[i].state != this.stateChanges[i]) {
                    if (this.stateChanges[i] == null) {
                        alert('선택되지 않은 상태가 있습니다.');
                        return;
                    }
                    let data = {
                        orderIdx: this.orders[i].orderIdx,
                        state: this.stateChanges[i],
                    }
                    states.push(data);
                }
            }
            if (states.length == 0) {
                alert('수정할 사항이 없습니다');
                return;
            }
            axios.patch(`/api/order/update`, states)
                .then(res => {
                    if (res.data.length == 0) {
                        alert('변경하셨습니다');
                        this.getOrder();
                    } else {
                        alert(`미완료된 변경(총 ${res.data.length}건)\n주문번호 : ${res.data}`)
                    }
                }).catch(err => {
                    console.log(err)
                })

        },
    },
    watch: { //변수 값이 변경될 때 연산을 처리하거나 변수 값에 따라 화면을 제어할 때 사용
        options: {
            handler() {
                this.getOrder();
            },
            deep: true,
        },
        search: {
            handler() {
                this.searchWord1 = '';
                this.searchWord2 = '';
                this.stateSelected = null;
                if (this.search == 'orderDate') {
                    let date = new Date();
                    this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                    this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
            }
        },

    },
}
