import axios from 'axios';
export default {
    data() {
        return {
            types: [{
                text: '기준 선택',
                value: null,
            }, {
                text: 'OUTER',
                value: 'outer',
            }, {
                text: 'OUTER>자켓',
                value: 'outer;jacket',
            }, {
                text: 'OUTER>가디건',
                value: 'outer;cardigan',
            }, {
                text: 'OUTER>점퍼',
                value: 'outer;jumper',
            }, {
                text: 'OUTER>코트',
                value: 'outer;coat',
            }, {
                text: 'TOP',
                value: 'top;all',
            }, {
                text: 'TOP>슬리브리스',
                value: 'top;sleeveless',
            }, {
                text: 'TOP>티셔츠',
                value: 'top;tshirts',
            }, {
                text: 'TOP>니트',
                value: 'top;knit',
            }, {
                text: 'TOP>셔츠',
                value: 'top;shirt',
            }, {
                text: 'TOP>블라우스',
                value: 'top;blouse',
            }, {
                text: 'TOP>맨투맨',
                value: 'top;mtm',
            }, {
                text: 'PANTS',
                value: 'pants;all',
            }, {
                text: 'PANTS>슬랙스',
                value: 'pants;slacks',
            }, {
                text: 'PANTS>데님',
                value: 'pants;denim',
            }, {
                text: 'PANTS>면',
                value: 'pants;cotton',
            }, {
                text: 'PANTS>SHORT',
                value: 'pants;shorts',
            }, {
                text: 'SKIRT',
                value: 'skirt;all',
            }, {
                text: 'SKIRT>미니',
                value: 'skirt;mini',
            }, {
                text: 'SKIRT>미디/롱',
                value: 'skirt;midi-long',
            }, {
                text: 'DRESS',
                value: 'dress;all',
            }, {
                text: 'DRESS>원피스',
                value: 'dress;ops',
            }, {
                text: 'DRESS>점프수트',
                value: 'dress;jumpSuit',
            }, ],
            typeSelected: null,

            productName: null,
            minPrice: 0,
            maxPrice: 9999999,

            searchOrders: [{
                    text: '기준 선택',
                    value: null,
                },
                {
                    text: '신상품 순',
                    value: 'regDate desc',
                },
                {
                    text: '낮은 가격 순',
                    value: 'price asc',
                },
                {
                    text: '높은 가격 순',
                    value: 'price desc',
                },
            ],
            searchOrder: null,

            page: 1,
            itemsPerPage: 12,
            pageLength: 0,
            visibleLength: 5,
            totalLength: 0,
            products: [],
            noSearch: false,
            dialog: false,

        }
    },
    methods: {
        search(category) {
            this.selectedCategory = category;
        },
        moveToDetail(num) {
            this.$router.push(`/productDetail/${num}`)
        },
        searchProduct() {
            if (this.page != 1) {
                this.page = 1;
            } else {
                this.getProduct();
            }
        },
        getProduct() {
            this.noSearch = false;
            let type1 = null;
            let type2 = null;
            if (this.typeSelected != null) {
                type1 = this.typeSelected.split(';')[0];
                type2 = this.typeSelected.split(';')[1];
            }
            axios({
                method: 'get',
                url: `/api/product/getProductList`,
                params: {
                    page: this.page,
                    perPage: this.itemsPerPage,
                    type1: type1,
                    type2: type2,
                    searchWord: this.productName,
                    minPrice: this.minPrice,
                    maxPrice: this.maxPrice,
                    searchOrder: this.searchOrder,
                }
            }).then(res => {
                this.products = res.data.productList;
                this.pageLength = Math.ceil(res.data.count / this.itemsPerPage);
                this.totalLength = res.data.count;
            }).catch((err) => {
                this.products = [];
                this.pageLength = 0;
                this.noSearch = true;
                console.log(err);
            })
        },
        pricePolicy() {
            if (this.minPrice < 0 || this.minPrice > 9999999 || this.minPrice != Math.round(this.minPrice)) {
                alert('0원 ~ 9,999,999원의 상품만 검색이 가능합니다');
                this.minPrice = 0;
            } else if (this.maxPrice < 0 || this.maxPrice > 9999999 || this.maxPrice != Math.round(this.maxPrice)) {
                alert('0원 ~ 9,999,999원의 상품만 검색이 가능합니다');
                this.maxPrice = 9999999;
            }
        },
        reset() {
            this.typeSelected = null;
            this.productName = null;
            this.minPrice = 0;
            this.maxPrice = 9999999;
            this.searchOrder = null;
            if (this.page != 1) {
                this.page = 1;
            } else {
                this.getProduct();
            }
        },
    },
    watch: {
        page: {
            handler() {
                this.getProduct();
            }
        }
    },
    mounted() {
        this.mainCategory = this.$route.params.id;
        this.search(this.$route.params.sub)
    }
}
