import axios from 'axios'
import HideId from '@/components/HideId.vue'
import DateDisplay from '@/components/DateDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        HideId,
        DateDisplay,
    },
    data() {
        return {
            totalContents: 0,
            contents: [],
            nameList: [],
            options: {},
            loading: true,
            headers: [{
                text: '번호',
                value: 'reviewNo',
                width: '5%',
                align: 'center',
                divider: true
            }, {
                text: '이미지',
                value: 'image',
                width: '10%',
                align: 'center',
                divider: true
            }, {
                text: '상품명',
                value: 'productName',
                width: '10%',
                align: 'center',
                divider: true
            }, {
                text: '별점',
                value: 'star',
                width: '10%',
                align: 'center',
                divider: true
            }, {
                text: '후기',
                value: 'content',
                width: '51%',
                align: 'center',
                divider: true
            }, {
                text: '작성자',
                value: 'id',
                width: '7%',
                align: 'center',
                divider: true
            }, {
                text: '작성일',
                value: 'regDate',
                width: '7%',
                align: 'center'
            }],
            searches: [{
                text: '상품명',
                value: 'productName'
            }, {
                text: '작성자',
                value: 'id'
            }],
            search: 'id',
            searchWord: ''
        }
    },
    methods: {
        getReview() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options;
            axios({
                method: 'get',
                url: `/api/review/getAllReviews`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    search: this.search,
                    searchWord: this.searchWord,
                    productNo: 0,
                }
            }).then(res => {
                this.contents = res.data.reviewList;
                this.totalContents = res.data.count;
            }).finally(() => {
                this.loading = false;
            })
        },
        searchReview() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getReview();
            }
        },
        deleteReview(num) {
            axios.delete(`/api/review/delete/${num}`)
                .then(() => {
                    alert("삭제가 완료되었습니다.")
                    this.getReview();
                }).catch(err => {
                    console.log(err);
                })
        },
        updateReview(num) {
            this.$router.push(`/updatePost/review/${num}`);
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getReview()
            },
            deep: true
        }
    }
}
