export default {
    data() {
        return {
            pagename: '',
        }
    },
    methods: {
        currentURL() {
            let link = document.location.href;
            link = link.substring(26, link.length);
            let pageList = [{
                text: '상품문의',
                value: 'productQnA',
            }, {
                text: '배송문의',
                value: 'deliveryQnA',
            }, {
                text: '배송 전 변경/취소',
                value: 'beforeDeliveryQnA',
            }, {
                text: '배송 후 교환/반품',
                value: 'afterDeliveryQnA',
            }, ]
            for (let i = 0; i < 4; i++) {
                if (link == pageList[i].value) {
                    this.pagename = pageList[i].text;
                }
            }
        },
    },
    computed: {},
    watch: {
        '$route'() {
            this.currentURL();
        }
    },
    mounted() {
        this.currentURL();
    }
}
