import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
import QnATitleDisplay from '@/components/QnATitleDisplay.vue'

export default {
    components: {
        DateDisplay,
        QnATitleDisplay,
    },
    props: ['id'],
    data() {
        return {
            totalContents: 0,
            contents: [],
            options: {},
            loading: true,
            headers: [{
                text: '번호',
                value: 'qnaNo',
                width: '10%',
                align: 'center',
                divider: true,
            }, {
                text: '상품명',
                value: 'productName',
                width: '20%',
                align: 'center',
                divider: true
            }, {
                text: '제목',
                value: 'type',
                width: '55%',
                align: 'center',
                divider: true
            }, {
                text: '작성일',
                value: 'regDate',
                width: '15%',
                align: 'center',
            }, ],
            searches: [{
                text: '상품명',
                value: 'productName'
            }],
            search: 'productName',
            searchWord: '',
        }
    },
    methods: {
        getQnA() {
            this.loading = true
            const {
                page,
                itemsPerPage
            } = this.options
            axios.get(`/api/qna/getQnaList`, {
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    search: this.search,
                    searchWord: this.searchWord,
                    productNo: 0,
                    id: this.id,
                }
            }).then((res) => {
                this.contents = res.data.productQnaList;
                this.totalContents = res.data.count;
            }).finally(() => {
                this.loading = false;
            })
        },
        searchQnA() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getQnA();
            }
        },
        moveto(item) {
            this.$router.push(`/qna/${item.qnaNo}`)
        },
    },
    watch: {
        options: {
            handler() {
                this.getQnA()
            },
            deep: true,
        },
    },
}
