import Footer from '@/components/Footer.vue'
import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    data() {
        return {
            fab: false,
            dialog: false,
            dialog2: false,
            message: '',
            orderNo: '',
            orderState: '',
            messages: [],
            textHeight: 200,
            previousMessage: '',
            pages: [{
                    name: 'OUTER',
                    to: '/productList/outer/all',
                    items: [{
                        title: '재킷',
                        to: '/productList/outer/jacket',
                    }, {
                        title: '가디건',
                        to: '/productList/outer/cardigan',
                    }, {
                        title: '점퍼',
                        to: '/productList/outer/jumper',
                    }, {
                        title: '코트',
                        to: '/productList/outer/coat',
                    }, ],
                }, {
                    name: 'TOP',
                    to: '/productList/top/all',
                    items: [{
                        title: '슬리브리스',
                        to: '/productList/top/sleeveless',
                    }, {
                        title: '티셔츠',
                        to: '/productList/top/tshirts',
                    }, {
                        title: '니트',
                        to: '/productList/top/knit',
                    }, {
                        title: '셔츠',
                        to: '/productList/top/shirt',
                    }, {
                        title: '블라우스',
                        to: '/productList/top/blouse',
                    }, {
                        title: '맨투맨',
                        to: '/productList/top/mtm',
                    }, ],
                }, {
                    name: 'PANTS',
                    to: '/productList/pants/all',
                    items: [{
                        title: '슬랙스',
                        to: '/productList/pants/slacks',
                    }, {
                        title: '데님',
                        to: '/productList/pants/denim',
                    }, {
                        title: '면',
                        to: '/productList/pants/cotton',
                    }, {
                        title: 'SHORT',
                        to: '/productList/pants/shorts',
                    }, ],
                }, {
                    name: 'SKIRT',
                    to: '/productList/skirt/all',
                    items: [{
                        title: '미니',
                        to: '/productList/skirt/mini',
                    }, {
                        title: '미디/롱',
                        to: '/productList/skirt/midi-long',
                    }, ],
                }, {
                    name: 'DRESS',
                    to: '/productList/dress/all',
                    items: [{
                        title: '원피스',
                        to: '/productList/dress/ops',
                    }, {
                        title: '점프수트',
                        to: '/productList/dress/jumpSuit',
                    }, ],
                },

                {
                    name: 'Community',
                    to: '/community/notice',
                    items: [{
                            title: 'NOTICE',
                            to: '/community/notice',
                        },
                        {
                            title: 'REVIEW',
                            to: '/community/review',
                        },
                        {
                            title: 'FAQ',
                            to: '/community/faq',
                        },
                        {
                            title: '상품문의',
                            to: '/qna/productQnA',
                        },
                        {
                            title: '배송문의',
                            to: '/qna/deliveryQnA',
                        },
                        {
                            title: '배송 전 변경/취소',
                            to: '/qna/beforeDeliveryQnA',
                        },
                        {
                            title: '배송 후 교환/반품',
                            to: '/qna/afterDeliveryQnA',
                        },
                    ],
                },
            ],
        }
    },
    components: {
        Footer,
    },
    methods: {
        onScroll(e) {
            if (typeof window === 'undefined') return
            const top = window.pageYOffset || e.target.scrollTop || 0
            this.fab = top > 20
        },
        toTop() {
            this.$vuetify.goTo(0)
        },
        reset() {
            this.messages = [];
            this.message = '';
            this.messages.push({
                text: '안녕하세요 spring 입니다. 무엇을 도와드릴까요?',
                author: 'server'
            });
            this.previousMessage = '안녕하세요 spring 입니다. 무엇을 도와드릴까요?';
        },
        searchOrder() {
            axios.get(`/api/order/getOrder/${this.orderNo}`)
                .then(res => {
                    this.orderState = res.data;
                }).catch(() => {
                    this.orderState = '해당 주문이 없습니다';
                })
                .finally(
                    this.orderNo = ''
                )
        },
        selectMessage(item, selected) {
            this.message = selected;
            if (item.text == '주소를 변경할 주문의 현재 배송 상태를 선택해주세요') {
                this.message = '주소변경' + selected;
            }
            if (item.text == '배송 상태를 선택해주세요') {
                this.message = '배송문의' + selected;
            }
            this.sendMessage();
        },
        sendMessage() {
            if (this.message == '') {
                return;
            }
            this.messages.push({
                text: this.message,
                author: 'client'
            });

            if (this.message == '종료') {
                this.dialog = false;
                return;
            }
            let id = null;
            if (this.getLogin == null) {
                var timestamp = new Date().getUTCMilliseconds();
                id = 'tempUser' + timestamp;
            } else {
                id = this.getLogin.user.id;
            }
            axios({
                    method: 'post',
                    url: `/api/chatbot/chatbot`,
                    params: {
                        query: this.message,
                        id: id,
                    }
                })
                .then(res => {
                    this.previousMessage = res.data.description.replace(/(?:\r\n|\r|\n)/g, '<br />');
                    if (res.data.url != undefined) {
                        this.messages.push({
                            text: res.data.description.replace(/(?:\r\n|\r|\n)/g, '<br />'),
                            buttons: res.data.buttonList,
                            url: res.data.url.substring(21),
                            author: 'server'
                        });
                    } else {
                        this.messages.push({
                            text: res.data.description.replace(/(?:\r\n|\r|\n)/g, '<br />'),
                            buttons: res.data.buttonList,
                            author: 'server'
                        });
                    }
                    this.message = '';
                }).catch(err => {
                    console.log(err);
                })
                .finally(() => {
                    let container = this.$el.querySelector("#test");
                    container.scrollTop = container.scrollHeight;
                })
        },
        moveToUrl(url) {
            this.$router.replace(url);
        },
        itemHeight(item) {
            console.log(item)

            if (item) {
                if (item.text.length > 100) {
                    return 200
                } else
                    return 100
            } else
                return 50
        },
        ...LoginStore.mapActions(['Logout']),
        signOut() {
            this.Logout();
            if (this.$route.path !== '/') {
                this.$router.push('/');
            }
        }
    },
    computed: {
        width() {
            switch (this.$vuetify.breakpoint.name) {
                case 'xs':
                    return false;
                case 'sm':
                    return true;
                case 'md':
                    return true;
                case 'lg':
                    return true;
                case 'xl':
                    return true;
                default:
                    return true;
            }
        },
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() {
        this.reset();
    }
}
