import axios from 'axios'
import DateDisplay from '@/components/DateDisplay.vue'
export default {
    components: {
        DateDisplay,
    },
    data() {
        return {
            totalContents: 0,
            loading: false,
            options: {},
            headers: [{
                text: '상품번호',
                value: 'productNo',
                divider: true,
                align: 'center',
                width: '6%',
            }, {
                text: '상품명',
                value: 'productName',
                divider: true,
                align: 'center',
                width: '15%',
            }, {
                text: '대분류',
                value: 'type1',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '소분류',
                value: 'type2',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '이미지',
                value: 'imageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '가격',
                value: 'price',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '할인',
                value: 'discount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '색상',
                value: 'color',
                divider: true,
                align: 'center',
                width: '9%',
            }, {
                text: '사이즈',
                value: 'size',
                divider: true,
                align: 'center',
                width: '6%',
            }, {
                text: '수량',
                value: 'amount',
                divider: true,
                align: 'center',
                width: '5%',
            }, {
                text: '등록 날짜',
                value: 'regDate',
                divider: true,
                align: 'center',
                width: '7%',
            }, {
                text: '상세 이미지',
                value: 'detailImageName',
                divider: true,
                align: 'center',
                width: '10%',
            }, {
                text: '판매 여부',
                value: 'onSale',
                divider: true,
                align: 'center',
                width: '7%',
            }, {
                text: '수정',
                value: 'btn',
                align: 'center',
                width: '5%',
            }],

            searches: [{
                text: '상품번호',
                value: 'productNo',
            }, {
                text: '상품명',
                value: 'productName',
            }, {
                text: '상품 분류',
                value: 'type',
            }, {
                text: '가격',
                value: 'price',
            }, {
                text: '재고',
                value: 'amount',
            }, {
                text: '등록일',
                value: 'regDate',
            }, ],
            search: 'productNo',
            searchWord1: '',
            searchWord2: '',

            products: [],
            colorList: [],
            sizeList: [],

            types: [{
                text: '기준 선택',
                value: null,
            }, {
                text: 'OUTER',
                value: 'outer',
            }, {
                text: 'OUTER>자켓',
                value: 'outer;jacket',
            }, {
                text: 'OUTER>가디건',
                value: 'outer;cardigan',
            }, {
                text: 'OUTER>점퍼',
                value: 'outer;jumper',
            }, {
                text: 'OUTER>코트',
                value: 'outer;coat',
            }, {
                text: 'TOP',
                value: 'top;all',
            }, {
                text: 'TOP>슬리브리스',
                value: 'top;sleeveless',
            }, {
                text: 'TOP>티셔츠',
                value: 'top;tshirts',
            }, {
                text: 'TOP>니트',
                value: 'top;knit',
            }, {
                text: 'TOP>셔츠',
                value: 'top;shirt',
            }, {
                text: 'TOP>블라우스',
                value: 'top;blouse',
            }, {
                text: 'TOP>맨투맨',
                value: 'top;mtm',
            }, {
                text: 'PANTS',
                value: 'pants;all',
            }, {
                text: 'PANTS>슬랙스',
                value: 'pants;slacks',
            }, {
                text: 'PANTS>데님',
                value: 'pants;denim',
            }, {
                text: 'PANTS>면',
                value: 'pants;cotton',
            }, {
                text: 'PANTS>SHORT',
                value: 'pants;shorts',
            }, {
                text: 'SKIRT',
                value: 'skirt;all',
            }, {
                text: 'SKIRT>미니',
                value: 'skirt;mini',
            }, {
                text: 'SKIRT>미디/롱',
                value: 'skirt;midi-long',
            }, {
                text: 'DRESS',
                value: 'dress;all',
            }, {
                text: 'DRESS>원피스',
                value: 'dress;ops',
            }, {
                text: 'DRESS>점프수트',
                value: 'dress;jumpSuit',
            }, ],
            typeSelected: null,

            menu1: false,
            menu2: false,
        }
    },
    methods: {
        getProduct() {
            let type1 = null;
            let type2 = null;
            if (this.typeSelected != null) {
                type1 = this.typeSelected.split(';')[0];
                type2 = this.typeSelected.split(';')[1];
            }
            const {
                page,
                itemsPerPage
            } = this.options
            axios({
                method: 'get',
                url: `/api/product/getProductAll`,
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    type1: type1,
                    type2: type2,
                    search: this.search,
                    searchWord1: this.searchWord1,
                    searchWord2: this.searchWord2,
                }
            }).then(res => {
                this.products = res.data.productList;
                this.totalContents = res.data.count;
            }).catch((err) => {
                this.products = [];
                this.totalContents = 0;
                this.noSearch = true;
                console.log(err);
            })
        },
        searchProduct() {
            if (this.options.page != 1) {
                this.options.page = 1;
            } else {
                this.getProduct();
            }
        },
        reset() {
            this.searchWord1 = null;
            this.searchWord2 = null;
            this.typeSelected = null;
            if (this.search == 'price') {
                this.searchWord1 = 0;
                this.searchWord2 = 9999999;
            } else if (this.search == 'amount') {
                this.searchWord1 = 0;
                this.searchWord2 = 9999;
            } else if (this.search == 'regDate') {
                let date = new Date();
                this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getProduct();
        },
        changeOnSale(item) {
            axios.patch(`/api/product/updateOnSale/${item.productNo}`)
                .then(() => {
                    alert('판매 여부가 변경되었습니다');
                    this.getProduct();
                }).catch(err => {
                    alert('변경 실패했습니다.')
                    console.log(err);
                })
        },
        searchPolicy() {
            if (this.search == 'price') {
                if (this.searchWord1 < 0 || this.searchWord1 > 9999999 || this.searchWord1 != Math.round(this.searchWord1)) {
                    alert('가격 제한 : 0원 ~ 9,999,999원');
                    this.searchWord1 = 0;
                } else if (this.searchWord2 < 0 || this.searchWord2 > 9999999 || this.searchWord2 != Math.round(this.searchWord2)) {
                    alert('가격 제한 : 0원 ~ 9,999,999원');
                    this.searchWord2 = 9999999;
                }
            } else {
                if (this.searchWord1 < 0 || this.searchWord1 > 9999 || this.searchWord1 != Math.round(this.searchWord1)) {
                    alert('개수 제한 : 0개 ~ 9,999개');
                    this.searchWord1 = 0;
                } else if (this.searchWord2 < 0 || this.searchWord2 > 9999 || this.searchWord2 != Math.round(this.searchWord2)) {
                    alert('개수 제한 : 0개 ~ 9,999개');
                    this.searchWord2 = 9999;
                }
            }
        },
        AddComma(num) {
            var regexp = /\B(?=(\d{3})+(?!\d))/g;
            return `${num}`.toString().replace(regexp, ",");
        },
    },
    watch: { //변수 값이 변경될 때 연산을 처리하거나 변수 값에 따라 화면을 제어할 때 사용
        options: {
            handler() {
                this.getProduct();
            },
            deep: true,
        },
        search: {
            handler() {
                this.searchWord1 = '';
                this.searchWord2 = '';
                this.typeSelected = null;
                if (this.search == 'price') {
                    this.searchWord1 = 0;
                    this.searchWord2 = 9999999;
                } else if (this.search == 'amount') {
                    this.searchWord1 = 0;
                    this.searchWord2 = 9999;
                } else if (this.search == 'regDate') {
                    let date = new Date();
                    this.searchWord1 = `${date.getFullYear()-5}-${date.getMonth()+1}-${date.getDate()}`;
                    this.searchWord2 = `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
            }
        },

    },
}
